body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(33, 34, 39, 255);
  color: #ffffff;
  font-size: 1em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    margin: 10px;
    display: flex;
    justify-content: space-between;
}

.nav-items{
    list-style-type: none;
    display: flex;
}

.text-link{
    color: inherit;
    text-decoration: inherit;
}

.nav-items > li{
    padding: 10px;
}

.home-img{
    max-height: 35em;
}
.true-story{
    text-align: center;
    padding: 0 15em;
    margin-bottom: 5em;
}

.img-ctn{
    background-color: aliceblue;
    height: 20em;
    width: 100%;
}
.front-page{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

h1{
    font-size: 3em;
}
h1 > span{
    color: rgb(37, 74, 168);
}
.content-area{
    padding: 30px;
}


.question-header-ctn{
    text-align: center;
}

.qt-h{
    text-align: center;
}
.quote-ctn{
    margin: 5em;
}
.question-ctn{
    width: 40em;
    height: 20em;
}

.containers{
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer{
    height: 3em;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    position: fixed;
    bottom: 0;
}
